import '../styles/App.css';
import '../styles/landingPage.css';
import '../styles/footer.css';
import '../styles/animation.css';
import '../styles/splide.min.css';
import '../styles/navbar.css';
import Carrossel from '../components/carrossel';

function LandingPage() {

  return (  
    <div className='body-area'>
        <main>
          <section className="home-container">
            <div>
              <div className='home-container-text'>
                {/*<FlipText />*/}
                <div className='logo-modelOps'>
                  <img src={process.env.PUBLIC_URL + "/img/Marca_Modelops.png"} alt="Logo ONS" />
                  <h1>ModelOPS</h1>
                </div>
                <p className='sub-title'>Governana, automao e gesto completa de modelos inteligentes.</p>
                <div>
                  <button className='btn-container'><a href="https://modelops-ui-modelops-tst.apps.rosa.rosa-dev.wmaj.p3.openshiftapps.com/dashboard">Utilizar Sistema</a></button>
                  <button className='btn-container btn-container-secondary'><a href="#">Em DEV</a></button>
                </div>
              </div>
              <img src={process.env.PUBLIC_URL + "/img/Logo_ModelOps_Sem_Texto.svg"} alt="Carga inicial" />
            </div>
          </section>
          <section id='home-about'>
            <h2>Principais <span>Benefcios</span></h2>
            <Carrossel />
          </section>
          <section id='home-services'>
            <h2>Servios</h2>
            <div className='services-container'>
              <div className='service-item'>
                <img src={process.env.PUBLIC_URL + "/img/esboo_modelOps.png"} alt="Carga inicial" />
                <div>
                  <h3>Amplie o poder dos seus dados</h3>
                  <p>Seus dados so alterados o tempo todo.
                    O DBSync habilita seus aplicativos a reagirem a cada alterao de dados.
                    Com o monitoramento em tempo real dos dados, o DBSync transmite os dados para o repositrio de destino assim que a transao  confirmada.
                    Utilize a capacidade do CDC integrado com o Kafka para realizar o decoupling das suas aplicaes, sem precisar alterar nenhuma linha de cdigo.</p>
                </div>
              </div>
              <div className='service-item'>
                <div>
                  <h3>Reduza esforo de implementaes</h3>
                  <p>Como o DBSync tem a capacidade de escrever dados diretamente nas bases de dados de destino, voc no precisa se preocupar em manter a escrita dos dados nas suas aplicaes.
                  Basta cadastrar as conexes com as bases de dados de origem e destino, que o DBSync faz o restante do trabalho.</p>
                </div>
                <img src={process.env.PUBLIC_URL + "/img/telaGestaoConectores.png"} alt="Low-code" />
              </div>
              <div className='service-item'>
                <img src={process.env.PUBLIC_URL + "/img/telaConectores.png"} alt="Replicao" />
                <div>
                  <h3>Monitore o status de cada conector</h3>
                  <p>Com a interface do DBSync interagindo com a API do DEBEZIUM,  possvel monitorar o status de cada conector, bem como as suas definies, de forma protegida.
                  Caso seja necessria alguma manuteno, existe tambm a possibilidade de pausar e reiniciar a sincronizao.
                  Caso exista alguma falha no conector,  possvel saber os detalhes da exceo clicando no status de falha.</p>
                </div>
              </div>
              <div className='service-item'>
                <div>
                  <h3>Ganhe velocidade</h3>
                  <p>Quando bem configurado, o DBSync  rpido.
                    Isso significa que a entrega dos dados beira o tempo real.
                    O DBSync usa o Kafka, que  robusto, escalvel e lida com grandes volumes de dados rapidamente. Alm disso, usa a infraestrutura do Openshift, que pode ser escalada para persistir esses dados to velozmente quanto so lidos.</p>
                </div>
                <img src={process.env.PUBLIC_URL + "/img/telaGestaoParametros.png"} alt="Suporte 24/7" />
              </div>
              <div className='service-item'>
                <img src={process.env.PUBLIC_URL + "/img/telaGestaoParametros.png"} alt="Suporte 24/7" />
                <div>
                  <h3>Evite a perda de dados</h3>
                  <p>Mesmo em um cenrio de downtime das aplicaes, aps o reestabelecimento dos recursos o DBSync volta a sincronizar os dados. Com a segurana da entrega dos dados via Kafka, cada cliente tem gravado exatamente o ltimo dado que foi lido e entregue. Alm disso, a aplicao est coberta pelo sobreaviso e envia alertas de falhas via Telegram e e-mail.</p>
                </div>
              </div>
              <div className='service-item'>
                <div>
                  <h3>Mltiplos conectores</h3>
                  <p>Com o DBSync e seus conectores, habilitamos que os tipos de bases de dados de origem e destino sejam diferentes. Por exemplo, abrimos a possibilidade de que os dados sejam de origem SqlServer e sejam escritos em um MongoDB, no destino. Como o DBSync usa o DEBEZIUM, o modelo de mensagem produzida fica na notao JSON, tornando simples a leitura e persistncia dos dados.</p>
                </div>
                <img src={process.env.PUBLIC_URL + "/img/telaGestaoParametros.png"} alt="Suporte 24/7" />
              </div>
            </div>
          </section>
        </main>
    </div>
  );
}

export default LandingPage;