import { Splide, SplideSlide } from '@splidejs/react-splide';

export default function Carrossel() {
  return (
    <div style={{ width: '100%', maxWidth: '676px', margin: '0 auto' }}>
      <Splide
        options={{
          type: 'loop',
          perPage: 1,
          pagination: true,
          arrows: true,
          autoplay: true,
          interval: 5000,
          pauseOnHover: true,
        }}
        style={{ width: '100%' }}
      >
        <SplideSlide className='slide-updates'>
          <div>
            <img src={process.env.PUBLIC_URL + "/img/Eficiencia_Operacional.png"} alt="Imagem Eficincia Operacional" />
            <p className='title-slide'>Eficincia Operacional</p>
          </div>
          <p>Reduo significativa no tempo de preparao, execuo e extrao de resultados</p>
        </SplideSlide>
        <SplideSlide className='slide-updates'>
          <div>
            <img src={process.env.PUBLIC_URL + "/img/code-img.png"} alt="Imagem Low-Code" />
            <p className='title-slide'>Transparncia</p>
          </div>
          <p>Rastreabilidade completa de todas as execues e verses de modelos</p>
        </SplideSlide>
        <SplideSlide className='slide-updates'>
          <div>
            <img src={process.env.PUBLIC_URL + "/img/setas-icone.png"} alt="Imagem Replicao" />
            <p className='title-slide'>Otimizao de Recursos</p>
          </div>
          <p>Diminuio dos custos computacionais e de armazenamento</p>
        </SplideSlide>
        <SplideSlide className='slide-updates'>
          <div>
            <img src={process.env.PUBLIC_URL + "/img/Produtividade.png"} alt="Imagem Suporte" />
            <p className='title-slide'>Produtividade</p>
          </div>
          <p>Eliminao de retrabalhos e processos manuais repetitivos</p>
        </SplideSlide>
      </Splide>
    </div>
  );
}